import GlobalStyleSheet from '../../../../styleSheet';
import { Row, Col, ListGroup } from 'react-bootstrap'

const Footer = () => (
    <div>
        <div style={styles.footer}>
            <p className="text-center">
                Copyright@ 2020
                    <span style={{ fontSize: 'medium', fontWeight: 'bold', }}> Muhalik </span>
                - Online Shopping Website
            </p>
            <Row noGutters style={{ margin: '1% 6%' }}>
                <Col style={styles.col}>
                    <div className='list_header'>Services</div>
                    <div className='list_text'>List</div>
                    <div className='list_text'>List</div>
                    <div className='list_text'>List</div>
                </Col>
                <Col style={styles.col}>
                    <div className='list_header'>List</div>
                    <div className='list_text'>List</div>
                    <div className='list_text'>List</div>
                    <div className='list_text'>List</div>
                    <div className='list_text'>List</div>
                </Col>
                <Col style={styles.col}>
                    <div className='list_header'>List</div>
                    <div className='list_text'>List</div>
                    <div className='list_text'>List</div>
                    <div className='list_text'>List</div>
                </Col>
            </Row>
        </div>
        <style jsx>
            {`
                .list_header {
                    background: none;
                    font-weight: bold;
                    color: ${GlobalStyleSheet.primary_text_color};
                    font-size: 18px;
                    margin-bottom: 3%
                }
                .list_text {
                    background: none;
                    color: ${GlobalStyleSheet.primary_text_color};
                    font-size: ${GlobalStyleSheet.form_label_fontsize}
                }
                p {
                    padding-top: 2%;
                    color: white;
                    font-size: ${GlobalStyleSheet.form_label_fontsize}
                }
            `}
        </style>
    </div>
)

const styles = {
    footer: {
        background: `${GlobalStyleSheet.admin_primry_color}`,
    },
    col: {
        padding: '1% 4%',
    },
}

export default Footer;